@mixin font-face($font-family, $url, $weight) {
	@font-face {
		font-family: '#{$font-family}';
		src:
			url('../fonts/#{$url}.woff2') format('woff2'),
			url('../fonts/#{$url}.woff') format('woff');
		font-weight: #{$weight};
		font-display: swap;
		font-style: normal;
	}
}

//Bahnschrift
@include font-face(Bahnschrift, Bahnschrift-Regular, 400);
